.content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  font-family: "Pretendard";
  margin-top: 121px;
}
.content .boxcontainer {
  display: flex;
  flex-wrap: wrap; /* 화면 크기가 줄어들면 아이템들이 줄 바꿈 되도록 */
  justify-content: center;
  width: 100%;
}
.content .gadna {
  display: flex;
  gap: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%; /* 각 박스가 가로폭에 맞춰 자동으로 늘어날 수 있게 */
}

.content .boxes {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%; /* 전체 넓이를 100%로 설정 */
  background-color: transparent;
  border-radius: 10px;
  color: white;
  border: 1px solid #424242;
  height: 144px;
  justify-content: center;
}
.content .icon_text {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 51px;
  gap: 31px;
  height: 100%;
}
.content .icon {
  width: 45px;
  height: 52px;
}
.content .icon2 {
  width: 46.67px;
  height: 46.67px;
}

.content .titledesc {
  width: 100%;
  height: 70%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: flex-start;
}

.content .title {
  margin: 0;
  font-size: 20px;
  font-family: "outfit", sans-serif;
  font-weight: 600;
  letter-spacing: 0;
}

.content .desc1 {
  font-size: 16px;
  color: #ffffff50;
  letter-spacing: 0;
}

.content .desc2 {
  font-size: 11px;
  color: #6a7cff;
  letter-spacing: 0;
}
@media (max-width: 600px) {
  .content {
    margin-top: 50px;
  }
  .content .boxcontainer {
    flex-direction: column; /* 작은 화면에서는 세로로 쌓이게 */
    align-items: center;
    margin-top: 36px;
  }
  .content .gadna {
    display: flex;
    gap: 9px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 90%; /* 각 박스가 가로폭에 맞춰 자동으로 늘어날 수 있게 */
    height: 100%;
  }
  .content .boxes {
    width: 390px;
    height: 230px;
  }
  .content .icon_text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 0;
    gap: 0;
    width: 100%;
    padding-left: 29px;
    box-sizing: border-box;
  }

  .content .icon {
    width: 45px;
    height: 52px; /* 아이콘 크기를 화면 크기에 맞게 축소 */
    margin-top: 41px;
  }
  .content .icon2 {
    width: 46.67px;
    height: 46.67px;
    margin-top: 41px;
  }
  .content .titledesc {
    margin-top: 10px;
    height: 0;
    justify-content: normal;
    align-items: normal;
  }
  .content .title {
    font-size: 20px; /* 작은 화면에서는 글씨 크기를 줄임 */
    font-weight: 700;
  }
  .content .desc1 {
    font-size: 16px; /* 설명 텍스트 크기를 줄임 */
    margin-top: 9px;
  }
  .content .desc2 {
    font-size: 11px;
  }
}
@media (min-width: 600px) and (max-width: 1024px) {
    .content .gadna {
        display: flex;
        gap: 9px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90%; /* 각 박스가 가로폭에 맞춰 자동으로 늘어날 수 있게 */
        height: 100%;
      }
}
